.dashbpard-card{
    border: none;
    box-shadow: 2px 4px 12px 0px #f7f7f7;
    border-radius: 20px;
    background: #fff;
    margin-top: 30px;
    position: relative;
    border: 1px solid #f0f3f3;
}

.dashbpard-card .one, .two, .three, .four{ text-align: center; font-weight: 500; font-size: 24px;}
.dashbpard-card .one{color: #34bae9;}
.dashbpard-card .two{color: #42d9bf}
.dashbpard-card .three{color: #f83179}
.dashbpard-card .four{color: #fdc102}


